@import url(https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap);
.home-card {
  max-width: 500px;
  height: -webkit-max-content;
  height: max-content;
  margin: 26px auto !important;
  /* padding: 1px; */
  z-index: 100;
  box-shadow: 10px solid red;
}

.name-heading {
  cursor: pointer;
}

.material-icons {
  cursor: pointer;
}

.delete-icon {
  position: absolute;
  top: 0;
  right: 0;
  margin: 2px;
}

.comment-container {
  padding: 3px;
}

.comment-author {
  font-weight: bold;
}

.my-card {
  margin-top: 40px;
}

.auth-card {
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}

.input-field input[type="text"]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}

#toast-container {
  top: 30px !important;
  right: 20px !important;
  color: red !important;
}

/* container of profile */
.container {
  max-width: 550px;
  margin: 0px auto;
}

.profile-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin: 18px 0px;
  border-bottom: 1px solid grey;
  /* width: 1%; */
}

.profile-img {
  height: 160px;
  width: 160px;
  border-radius: 100%;
}

.profile-details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 108%;
}

.gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.item {
  width: 30%;
  /* border-radius: 100px; */
}

.input-filed {
  margin: 25px auto;
  padding: 20px;
  text-align: center;
  max-width: 500px;
}

#nav-mobile li:hover {
  background-color: none;
}

a {
  color: black !important;
}

.brand-logo,
h2 {
  font-family: "Grand Hotel", cursive;
}

.Follow-button {
    margin: 1rem;
}
